const state = {
	allPageInfo: {},
	allDictData: {},
	areaList: [],
	industryList: [],
	waterList: [],
};
const getters = {
	allPageInfo: state => state.allPageInfo,
	allDictData: state => state.allDictData,
	areaList: state => state.areaList,
	industryList: state => state.industryList,
	waterList: state => state.waterList,
};
const mutations = {
	setPageInfoM(state, payload) {
		state.allPageInfo = payload
	},
	setDictDataM(state, payload) {
		state.allDictData = payload
	},
	setAreaListM(state, payload) {
		state.areaList = payload
	},
	setIndustryListM(state, payload) {
		state.industryList = payload
	},
	setWaterListM(state, payload) {
		state.waterList = payload
	}
};
const actions = {
	setPageInfoA({ commit }, data) {
		commit('setPageInfoM', data);
	},
	setDictDataA({ commit }, data) {
		commit('setDictDataM', data);
	},
	setAreaListA({ commit }, data) {
		commit('setAreaListM', data);
	},
	setIndustryListA({ commit }, data) {
		commit('setIndustryListM', data);
	},
	setWaterListA({ commit }, data) {
		commit('setWaterListM', data);
	},
};
export default {
	namespaced: true,
	state,
	getters,
	mutations,
	actions
}